<template>
  <Row>
    <Col span="21">
      <Row v-highlight>
    <Col>
    <div class="i-editor-wrapper">
      <div class="i-editor-md">
        <div class="dev-md-content">
          <h3 id="GS_vNode"> <a href="#GS_vNode">#</a></h3>
          <p>
            <code>&lt;task-work-area&gt;</code>
             
            <code>&lt;svg&gt;</code>
            
          </p>
          <h3 id="YF_vNode"> <a href="#YF_vNode">#</a></h3>
          <pre>
            <code class="lang-html">&lt;task-work-area width="500" height="500" id="work_id"&gt;
    &lt;!--  --&gt;
    &lt;/task-work-area&gt;
    </code>
          </pre>
          <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3>
        </div>
      </div>
      <p>props</p><br>
      <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      <br><p>event</p><br>
      <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
    </div>
    </Col>
  </Row>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'GS_vNode',
          title:''
        },{
          uri:'YF_vNode',
          title:''
        },{
          uri:'API_vNode',
          title:'API'
        }],
        propsData:[{
          attribute: 'width',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'id',
          state: 'ID',
          mold: 'String | Number',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-over',
          exp: '',
          retu: '(event,)'
        },{
          eventname: 'on-mouse',
          exp: ' ',
          retu: '(event,ID)'
        }
        ]
      }
    }
  }
</script>




