<template>
  <Row>
    <Col span="21">
  <Row v-highlight>
    <Col>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="YS_vNode"> <a href="#YS_vNode">#</a></h3></div></div></div>
    <div class="vnode-col">
      <task-work-area width=600 height=400 id="areaID" v-on:on-drag-over="dragOver" v-on:on-mouse="mouseMenu" ref="area">
        <task-common-node :node="node" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"></task-common-node>
      </task-work-area>
    </div>
    </Col>
    <Col>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="GS_vNode"> <a href="#GS_vNode">#</a></h3><p><code>&lt;task-common-node&gt;</code>  <task-node></task-node></p>
      <h3 id="YF_vNode"> <a href="#YF_vNode">#</a></h3><pre><code class="lang-html">&lt;template&gt;
    &lt;div&gt;
      &lt;task-work-area width=600 height=400 id="areaID" v-on:on-drag-over="dragOver" v-on:on-mouse="mouseMenu" ref="area"&gt;
        &lt;task-common-node :node="node" v-on:on-select="selectlMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" :updateTem="updateCompleted" v-on:on-mouse="mouseNodeMenu"&gt;&lt;/task-common-node&gt;
      &lt;/task-work-area&gt;
    &lt;/div&gt;
&lt;/template&gt;
&lt;script&gt;
  export default {
    data () {
      return {
        startNode:null,
        node:{
          id: "node2",
          name: "2",
          positionX: 20,
          positionY: 100,
          state: "mistake",
          inPorts: [
            {
              id: "node2_1",
              isConnected: true
            },
            {
              id: "node2_3",
              isConnected: false
            }
          ],
          outPorts: [
            {
              id: "node2_4"
            },
            {
              id: "node2_5"
            }
          ]
        }
      }
    },
    methods: {
      selectlMethod: function (event, data, node) {
        console.log(event, data, node)
      },
      dragStart: function (event, node) {
        let nodeData = event.dataTransfer.getData('nodedata')
        console.log('', event.clientX, event.clientY, node, JSON.parse(nodeData))
        this.startNode = {id: node.id, positionX: event.clientX, positionY: event.clientY}
      },
      dragEnd: function (event, value) {
        let me = this
        if (value.id === this.node.id) {
          this.node.positionX = value.positionX + (event.clientX - me.startNode.positionX)
          this.node.positionY = value.positionY + (event.clientY - me.startNode.positionY)
        }
      },
      dragOver: function (event) {
        console.log('...', event.clientX, event.clientY)
      },
      updateCompleted: function () {
        // 
        this.$refs.curve.vReload()
      },
      mouseMenu: function (event, id) {
        console.log(event, id)
      },
      mouseNodeMenu: function (event, node) {
        console.log(event, node)
      }
    }
  }
&lt;/script&gt;
</code></pre>
      <p> <code>on-drag-start</code><code>v-on:on-drag-end</code><code>(x1,y1)</code><code>(x2,y2)</code><code>-</code> <code>(x2-x1) (y2-y1)</code><code>XY</code><code>node</code><code>positionX</code> <code>positionX</code></p>
      <p><code>:updateTem="updateCompleted"</code> nodeDOM<code>this.$refs.[ ref].vReload()</code></p>

      <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3></div></div>
      <p>props</p><br>
      <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      <br><p>event</p><br>
      <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
    </div>
    <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="DATA_vNode">node <a href="#DATA_vNode">#</a></h3>
      <pre><code class="lang-js">node: {
  id: [String, Number],//ID
  name: {
    type: [String, Number],
    default: ''
  },
  positionX: {//X
    type: [String, Number],
    default: 0
  },
  positionY: {//Y
    type: [String, Number],
    default: 0
  },
  icon: [String, Number],//
  state: {// [success,running,ready,mistake]
    type: [String, Number],
    default: 'ready'
  },
  inPorts: {//
    type: Array
  },
  outPorts: {//
    type: Array
  }
}
//inPorts,outPorts
</code></pre>
    </div></div></div>
    </Col>
  </Row>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'YS_vNode',
          title:''
        },{
          uri:'GS_vNode',
          title:''
        },{
          uri:'YF_vNode',
          title:''
        },{
          uri:'API_vNode',
          title:'API'
        },{
          uri:'DATA_vNode',
          title:'node'
          }],
        startNode:null,
        node:{
          id: "node2",
          name: "2",
          positionX: 20,
          positionY: 100,
          state: "mistake",
          inPorts: [
            {
              id: "node2_1",
              isConnected: true
            },
            {
              id: "node2_3",
              isConnected: false
            }
          ],
          outPorts: [
            {
              id: "node2_4"
            },
            {
              id: "node2_5"
            }
          ]
        },
        propsData:[{
          attribute: 'width',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'node',
          state: 'node',
          mold: 'Array',
          defau: '-'
        },{
          attribute: 'updateTem',
          state: 'vueupdate',
          mold: 'Function',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-start',
          exp: '',
          retu: '( event, node )'
        },{
          eventname: 'on-drag-end',
          exp: ' ',
          retu: '( event, node )'
        },{
          eventname: 'on-select',
          exp: '',
          retu: '( event, node, ref )'
        },{
          eventname: 'on-mouse',
          exp: '',
          retu: '( event, node )'
        }
        ]
      }
    },
    methods: {
      selectlMethod: function (event, data, node) {
        console.log(event, data, node)
      },
      dragStart: function (event, node) {
        let nodeData = event.dataTransfer.getData('nodedata')
        console.log('', event.clientX, event.clientY, node, JSON.parse(nodeData))
        this.startNode = {id: node.id, positionX: event.clientX, positionY: event.clientY}
      },
      dragEnd: function (event, value) {
        let me = this
        if (value.id === this.node.id) {
          this.node.positionX = value.positionX + (event.clientX - me.startNode.positionX)
          this.node.positionY = value.positionY + (event.clientY - me.startNode.positionY)
        }
      },
      dragOver: function (event) {
        console.log('...', event.clientX, event.clientY)
      },
      updateCompleted: function () {
        // 
        this.$refs.curve.vReload()
      },
      mouseMenu: function (event, id) {
        console.log(event, id)
      },
      mouseNodeMenu: function (event, node) {
        console.log(event, node)
      }
    }
  }
</script>
<style>
  .vnode-col{
    width: 620px;
    height: 420px;
    /* text-align: center; */
    border: 1px solid;
    border-style: dashed;
    padding: 5px;
    margin-left: 5px;
    background-color: #eee;
  }
</style>



