<template>
  <Row>
    <Col span="21">
    <div>
      <div class="i-editor-wrapper"><div class="i-editor-md"> <div class="dev-md-content"><h3 id="GS_vNode"> <a href="#GS_vNode">#</a></h3><p><code>&lt;task-in-port&gt;</code><code>&lt;node&gt;</code></p>
        <h3 id="YF_vNode"> <a href="#YF_vNode">#</a></h3><p> </p>
        <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3></div></div>
        <p>props</p><br>
        <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      </div>
    </div>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'YS_vNode',
          title:''
        },{
          uri:'GS_vNode',
          title:''
        },{
          uri:'YF_vNode',
          title:''
        },{
          uri:'API_vNode',
          title:'API'
        }],
        propsData:[{
          attribute: 'pid',
          state: 'ID',
          mold: 'String | Number',
          defau: '-'
        },{
          attribute: 'content',
          state: '',
          mold: 'String | Number',
          defau: ''
        },{
          attribute: 'isConnected',
          state: '',
          mold: 'Boolean',
          defau: 'false'
        }]
      }
    }
  }
</script>



