<template>
  <Row>
    <Col span="21">
      <div v-highlight>
        <div class="i-editor-wrapper">
          <div class="i-editor-md">
            <div class="dev-md-content">
              <h3 id="GS_vNode"> <a href="#GS_vNode">#</a></h3>
              <p>
                <code>&lt;task-node&gt;</code>
                
              </p>
              <h3 id="YF_vNode"> <a href="#YF_vNode">#</a></h3>
              <p> </p>
              <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3>
            </div>
          </div>
          <p>props</p><br>
          <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
          <br><p>event</p><br>
          <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
        </div>
        <div class="i-editor-wrapper">
          <div class="i-editor-md">
            <div class="dev-md-content">
              <h3 id="DATA_vNode">node <a href="#DATA_vNode">#</a></h3>
              <pre>
            <code class="lang-js">node: {
      id: [String, Number], //ID
      positionX: {//X
        type: Number,
        default: 0
      },
      positionY: {//Y
        type: Number,
        default: 0
      }
    }
    </code>
          </pre>
            </div>
          </div>
        </div>
      </div>
    </Col>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'YS_vNode',
          title:''
        },{
          uri:'GS_vNode',
          title:''
        },{
          uri:'YF_vNode',
          title:''
        },{
          uri:'API_vNode',
          title:'API'
        },{
          uri:'DATA_vNode',
          title:'node'
        }],
        propsData:[{
          attribute: 'width',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'node',
          state: 'node',
          mold: 'Array',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-start',
          exp: '',
          retu: '( event, node )'
        },{
          eventname: 'on-drag-end',
          exp: ' ',
          retu: '( event, node )'
        },{
          eventname: 'on-select',
          exp: '',
          retu: '( event, node, ref )'
        },{
          eventname: 'on-mouse',
          exp: '',
          retu: '( event, node )'
        }
        ]
      }
    }
  }
</script>



