<template>
  <Row>
    <Col span="21">
      <div v-highlight>
    <div class="i-editor-wrapper">
      <div class="i-editor-md">
        <div class="dev-md-content">
          <h3 id="KZXJD_vNode">
            
            <code>&lt;task-node&gt;</code>
            
            <a href="#KZXJD_vNode">#</a>
          </h3>
          <pre>
            <code class="lang-html">&lt;template&gt;
    &lt;node :node="node" width=180 height=30 v-on:on-select="selectNodeMethod" v-on:on-drag-start="dragStart" v-on:on-drag-end="dragEnd" v-on:updateTem="updateTemp" v-on:on-mouse="mouseMenu"&gt;
    &lt;!-- &lt;task-in-port&gt;&lt;task-out-port&gt; --&gt;
    &lt;!-- selectNodeMethod,dragStart,dragEnd,updateTemp,mouseMenu --&gt;
    &lt;/node&gt;
    &lt;/template&gt;
    &lt;script&gt;
    import mixinsNode from 'vue-task-node/dist/node'

    export default {
      mixins: [ mixinsNode ],
      name: 'node',
      data () {
        return {
        }
      },
      props: {
        node: {
          id: [String, Number],
          name: {
            type: [String, Number],
            default: ''
          },
          positionX: {
            type: Number,
            default: 0
          },
          positionY: {
            type: Number,
            default: 0
          }
        }
      }
    }
    &lt;/script&gt;
    </code>
          </pre>
          <blockquote>
            <p>
              :
              <code>selectNodeMethod</code>
              ,
              <code>dragStart</code>
              ,
              <code>dragEnd</code>
              ,
              <code>updateTemp</code>
              ,
              <code>mouseMenu</code>
              
              <code>mixinsNode</code>
              
            </p>
          </blockquote>
          <h3 id="API_vNode">API <a href="#API_vNode">#</a></h3>
          <p></p>
        </div>
      </div>
      <p>props</p><br>
      <Table border size="small" :columns="propsColumn" :data="propsData"></Table>
      <br><p>event</p><br>
      <Table border size="small" :columns="eventColumn" :data="eventData"></Table>
    </div>
  </div>
    </Col>
    <Col span="3">
    <Anchor show-ink>
      <AnchorLink v-for="item in lins" :key="item.uri" :href="'#'+ item.uri" :title="item.title" />
    </Anchor>
    </Col>
  </Row>
</template>
<script>
  import ApiTable from '../mixinx/ApiTable'
  export default {
    mixins: [ ApiTable],
    data () {
      return {
        lins:[{
          uri:'KZXJD_vNode',
          title:''
        },{
          uri:'API_vNode',
          title:'API'
        }],
        propsData:[{
          attribute: 'width',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'height',
          state: '',
          mold: 'String | Number',
          defau: '0'
        },{
          attribute: 'node',
          state: 'node',
          mold: 'Array',
          defau: '-'
        },{
          attribute: 'updateTem',
          state: 'vueupdate',
          mold: 'Function',
          defau: '-'
        }],
        eventData: [{
          eventname: 'on-drag-start',
          exp: '',
          retu: '( event, node )'
        },{
          eventname: 'on-drag-end',
          exp: ' ',
          retu: '( event, node )'
        },{
          eventname: 'on-select',
          exp: '',
          retu: '( event, node, ref )'
        },{
          eventname: 'on-mouse',
          exp: '',
          retu: '( event, node )'
        }
        ]
      }
    }
  }
</script>



